import React, { useRef, useEffect, useMemo } from 'react'
import {DoubleSide, Vector3, Vector2, AdditiveBlending} from 'three'
import {useThree, useFrame} from 'react-three-fiber'

const plane_size = 10000

function TangentGrid({position, orientation, target, mouse, cellSize}) {
    const { size } = useThree()
    const meshRef = useRef()

    useEffect(() => {
        if (meshRef.current) {
            const laPoint = position.clone().addScaledVector(orientation, 5)
            meshRef.current.lookAt(laPoint)
        }
    }, [position, orientation])

    const uniforms = useMemo(() => {
        return {
            vTarget: { value: target },
            planeSize: { value: new Vector2(plane_size, plane_size) },
            mouse: { value: mouse },
            cellSize: { value: cellSize},
        }
    }, [])

    useFrame(() => {
        uniforms.mouse.value.x = mouse.x
        uniforms.mouse.value.y = size.height - mouse.y
        uniforms.cellSize.value = cellSize
        uniforms.vTarget.value = target
    })

    const fragmentShader = `
        varying vec3 norm;
        varying vec2 pos;
        varying vec3 posW;
        varying vec2 vUv;
        varying vec3 target;
        varying vec2 px;

        uniform vec2 planeSize;
        uniform vec2 mouse;
        uniform float cellSize;

        float gridValue(vec2 coord){
            vec2 nCoord = (coord / cellSize);
            vec2 grid = abs(fract(nCoord) - 0.5) / fwidth(nCoord*2.95);
            float line = min(grid.x, grid.y);
            return 1.0 - min(line, 1.0);
        }

        float gridVal2(vec3 coord){
            vec3 nCoord = (coord / cellSize) - cellSize/4.;
            vec3 grid = abs(fract(nCoord) - 0.5);
            float line = max(max(grid.x, grid.y), grid.z);
            return 1. - step(.1, line);
        }

        void main() {
            vec4 blue = vec4(0., 0., 1.5, 0.5);
            float blendFactor = min(2.3 / length(fwidth(vUv)), 1.0);
            vec2 shiftedPos = pos + cellSize/2.;
            float gridVal = gridValue(shiftedPos);
            
            // go from screen coords to [0, 1]
            vec2 mouseOffset = (gl_FragCoord.xy - mouse) / planeSize.x;

            // float mouseToPoint = 1.;
            // float mouseToEdge = gridVal2(target);
            // float pointToEdge = gridVal2(posW); //1. - step(.55, length(fract(posW / cellSize)));
            float on = 0.; //min(min(pointToEdge, mouseToEdge), mouseToPoint);

            float spotlight = (1. - smoothstep(0.04, 0.065, length(vUv - 0.5 + mouseOffset)));

            gl_FragColor = blue * gridVal * spotlight * blendFactor + vec4(1., 0., 0., 0.85)*on;
        }
    `

    const vertexShader = `
        varying vec3 norm;
        varying vec2 pos;
        varying vec3 posW;
        varying vec2 vUv;
        varying vec3 target;
        varying vec2 px;

        uniform vec2 planeSize;
        uniform vec2 mouse;
        uniform vec3 vTarget;

        void main() {
            norm = normalize(normalMatrix * normal);
            pos = ((uv - 0.5) * planeSize);
            posW = position;
            target = vTarget;
            vUv = uv;
            px = (2.0*uv - 1.0)/2.0 * planeSize;

            gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
        }
    `

    return (
        <mesh ref={meshRef} position={position}>
            <planeGeometry attach="geometry" args={[plane_size, plane_size]}></planeGeometry>
            <shaderMaterial attach="material" extensions={{derivatives: true}} blending={AdditiveBlending} depthTest={false} depthWrite={true} transparent side={DoubleSide} uniforms={uniforms} fragmentShader={fragmentShader} vertexShader={vertexShader} />
        </mesh>
    )
}

export default TangentGrid